$base-color: #c6538c;
$bar-heigth:54px;
$grid-columns: 12;
$grid-gap: 1rem;


@mixin padder {
    padding-left: 1em;
    padding-right: 1em;
    gap: $grid-gap;
}

@mixin reset-paddings {
    padding: 0;
    margin: 0;
}

@mixin shadow-lg {
    box-shadow: 0 10px 15px -3px rgba(0,0,0,.125), 0 4px 6px -2px rgba(0,0,0,.125);
}

