@import "./variables.scss";
html {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: "Baloo Tammudu 2", cursive;
}

@mixin full($size) {
  width: 100%;
  //   max-width: $size;
}

nav {
  height: $bar-heigth;
  ul {
    @include reset-paddings;
    display: grid;
    grid-template-columns: repeat(3, 100px);
    grid-gap: $grid-gap;
    justify-content: center;
    justify-items: center;
    list-style: none;
    font-size: large;
    height: $bar-heigth;
    align-items: center;
    font-weight: 700;

    @media (min-width: 600px) {
      grid-template-columns: 1fr repeat(3, fit-content(50px));
      grid-gap: $grid-gap;
      justify-items: end;
      height: $bar-heigth;
    }
  }

  .fullwidth {
    grid-column: 1 / 4;

    @media (min-width: 600px) {
      grid-column: 1 / 2;
      justify-self: start;
    }
  }
}
// @for $j from 2 through $grid-columns {
//   @if $j == 'sidebar' {
//     grid-column: 1 / 2
//   }
// }
@for $i from 1 through $grid-columns {
  .grid-#{$i} {
    gap: 1rem;
    grid-template-columns: repeat($i, auto);
    grid-auto-rows: auto;
    grid-auto-flow: dense;
    min-height: 0; /* NEW */
    min-width: 0; 
    grid-auto-columns: auto;
  }
  .col-#{$i} {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-auto-columns: auto;
    display: grid;
    min-height: 0; /* NEW */
    min-width: 0; /* NEW; needed for Firefox */
    overflow: hidden; /* NEW */
    background: #dfe7e7;
    grid-column-start: auto;
    grid-row-start: auto;

   
  }
}

.grid {
  display: grid;
  grid-template-columns: 100%;
}

// styles
[class^="grid-"] {
  display: grid;
  //   grid-template-rows: 80px auto 80px;
}

.container {
  display: grid;
  margin-left: auto;
  margin-right: auto;
  @include padder;
}

