
@import '../app/utils/variables.scss';
.ui-counter {

    &__body {
        padding: 1em;
        border-radius: 5px;
        background-color: #f5f5f5;
        color: rgb(38, 38, 38);
        text-align: center;
        min-width: 0;
        word-wrap: break-word;
        background-color: #fff;
        background-clip: border-box;
        @include shadow-lg;
    
        &__name {
            font-size: 2rem;
            margin: 0;
            line-height: 1.5;
            font-weight: bold;
        }
    
        &__count {
            margin: 0;
            font-size: 3rem;
            font-weight: bold; 
            line-height: 1.5;
            margin-bottom: 10px;
            font-weight: 300;
        }

        &__button {
            height: 36px;
            background-color: #fff;
            border-radius: 3px;
            border: none;
            width: auto;
            font-weight: bold;
            text-transform: uppercase;
            outline: none;
            cursor: pointer;
        }
    }
}